*{
  margin: 0;
  padding: 0;
}
li{
  list-style: none;
}

#root{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  width: min(90%,1000px);
  header{
    margin-bottom: 15px;
    width: 100%;
    border: 1px solid #000;
    text-align: center;
    h1{
      padding: 10px 0;
      font-size: 2em;
      text-transform: uppercase;
    }
  }
  section{
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
    border: 1px solid #000;
    ul{
      overflow: auto;
      box-sizing: border-box;
      padding: 50px 0;
      width: 90%;
      max-height: 500px;
      border-bottom: 1px solid #000;
      li{
        margin: 0 auto;
        margin-bottom: 20px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        figure{
          width: 200px;
          height: 200px;
          margin-right: 30px;
          img{
            width: 100%;
            height: 100%;
          }
        }
        span{
          width: 200px;
          text-align: left;
          p{
  
          }
        }
      }
    }
    >div{
      position: relative;
      padding: 15px 0;
      width: 100%;
      text-align: center;
      >button{
        margin-right: 10px;
        width: 20px;
        height: 20px;
      }
      form{
        display: flex;
        position: absolute;
        left: 5%;
        top: 50%;
        transform: translateY(-50%);
        input{
          width: 50%;
        }
      }
    }
  }
}

